@import "../../styles/variables.sass";

$appBarOffset: 135px;

.Field {
  margin: 0 auto;

  @media (min-width: #{$break-sm}) {
    padding-right: 5em; }

  @media (min-width: #{$break-md}) {
    &[data-purchased-field="0"] {
      max-width: calc(100vh * (6/10) - #{$appBarOffset}); }

    &[data-purchased-field="1"] {
      max-width: calc(100vh * (8/12) - #{$appBarOffset}); }

    &[data-purchased-field="2"] {
      max-width: calc(100vh * (10/16) - #{$appBarOffset}); }

    &[data-purchased-field="3"] {
      max-width: calc(100vh * (12/18) - #{$appBarOffset}); } }

  .spacer {
    min-height: 8em;

    @media (min-width: #{$break-sm}) {
      min-height: 7.5em; } }

  .row {
    display: flex; }

  .react-transform-component {
    overflow: visible;
    width: auto; }

  // backgronud-size controls scaling of the dirt background image. The formula
  // for each step:
  // 100% * (1 / number of field columns) * (proportion of dirt image size to crop image size)
  .react-transform-element {
    display: block;
    height: auto;
    width: auto;

    .row-wrapper {
      background-image: url('../../img/ui/dirt.png');
      border: solid 1px #000;
      background-repeat: repeat;
      background-size: calc(100% * (1 / 6) * 1.5);
      image-rendering: pixelated; } }

  &[data-purchased-field="1"] {
    .react-transform-element {
      background-size: calc(100% * (1 / 8) * 1.5); } }

  &[data-purchased-field="2"] {
    .react-transform-element {
      background-size: calc(100% * (1 / 10) * 1.5); } }

  &[data-purchased-field="3"] {
    .react-transform-element {
      background-size: calc(100% * (1 / 12) * 1.5); } }

  .slider-wrapper {
    background: rgba(128, 128, 128, 0.75);
    border-radius: 2em;
    bottom: 13.5em;
    left: 50%;
    padding: 0 1em;
    position: fixed;
    transform: translateX(-50%);
    transition: left $duration-entering-screen $easing-ease-out;
    width: 250px;

    @media (min-width: #{$break-sm}) {
      bottom: 7.5em; }

    @media (min-width: #{$break-md}) {
      .menu-open & {
        left: calc(50vw + #{$sidebar-width / 2}); } }

    @media (max-width: #{$break-medium-phone}) {
      bottom: 11em; } }

  .zoom-controls {
    position: fixed;
    display: flex;
    flex-direction: column;
    bottom: 1em;

    &.right {
      right: 0.5em;

      @media (max-width: #{$break-medium-phone}) {
        right: 0.25em; }

      @media (min-width: #{$break-sm}) {
        bottom: 5.5em; } }

    &.left {
      left: 0.5em;

      @media (max-width: #{$break-medium-phone}) {
        left: 0.25em; }

      @media (min-width: #{$break-sm}) {
        left: auto;
        right: 0.5em; } }

    @media (max-width: #{$break-medium-phone}) {
      bottom: 0.25em; }

    button {
      margin: 0.5em; } }

  .MuiFormControl-root {
    align-items: center;
    display: flex;
    padding: 1em 0 0; } }
